import { FetchType, Operator, Property, PropertyFetch, PropertyType } from "../models/property.models";
import { datasource } from "./datasource";
import { fetchOperators } from "./operatorService";


const fetchTypes = async (): Promise<FetchType[]> => {
  const operators: FetchType[] = datasource.getTypes() as FetchType[];
  return Promise.resolve(operators);
};

export const fetchPropertyTypes = async (): Promise<PropertyType[]> => {
 
  const [types, operators] = await Promise.all([fetchTypes(), fetchOperators()]);
  
  const operatorMap = new Map(operators.map(op => [op.id, op]));

  const propertyTypes: PropertyType[] = types.map(type => ({
    ...type,
    operators: type.operators.reduce((acc: Operator[], opId) => {
      const op = operatorMap.get(opId);
      if (op) acc.push(op);
      return acc;
    }, [])
  }));

  return propertyTypes;
};


export const fetchProperties = async (): Promise<Property[]> => {

  const propertyTypes = await fetchPropertyTypes();
  if (propertyTypes.length === 0) {
    throw new Error("No property types found");
  }

  const propertyTypesMap = new Map(propertyTypes.map(pt => [pt.id, pt]));

  const rawProperties: PropertyFetch[] = await Promise.resolve(datasource.getProperties() as PropertyFetch[]);

  const fullProperties: Property[] = rawProperties.map(prop => {
    const property = propertyTypesMap.get(prop.type);
    if(!property) {
      throw new Error(`Invalid Property type ${prop.type} in Property ${prop.name}`);
    }

    return {
      ...prop,
      type: property,
    }}
  );

  return fullProperties;
};