import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, CircularProgress, styled, IconButton } from '@mui/material';
import { Property } from 'models/property.models';
import { Product } from 'models/product.models';
import { RootState, useAppDispatch } from 'store/store';
import { useSelector } from 'react-redux';
import { useEffect } from 'react';
import { deleteProductAsync, fetchProductsAsync, selectAllProducts } from 'store/slices/productSlice';
import { fetchPropertiesAsync, selectAllProperties } from 'store/slices/propertySlice';
import { selectFilters } from 'store/slices/filterSlice';
import DeleteIcon from '@mui/icons-material/Delete';

const StyledTableHead = styled(TableHead)(({ theme }) => ({
  backgroundImage: 'linear-gradient(45deg, rgba(138, 38, 249, 1.0), rgba(0, 196, 255, 1.0))',
  '.MuiTableCell-head': {
    color: theme.palette.common.white,
    textTransform: 'capitalize',
    fontWeight: theme.typography.fontWeightBold,
  },
}));

const ProductList = () => {
  const dispatch = useAppDispatch();

  const filters = useSelector(selectFilters);
  const products: Product[] = useSelector(selectAllProducts);
  const properties: Property[] = useSelector(selectAllProperties);
  const loadingProductsStatus = useSelector((state: RootState) => state.productsState.status);
  const loadingPropertiesStatus = useSelector((state: RootState) => state.propertiesState.status);

  const handleDelete = (productId: number) => {
    dispatch(deleteProductAsync(productId));
  };

  useEffect(() => {
    dispatch(fetchPropertiesAsync());
  }, [dispatch]);

  useEffect(() => {
    dispatch(fetchProductsAsync(filters));
  }, [dispatch, filters]);

  if (loadingProductsStatus === 'loading' || loadingPropertiesStatus === 'loading') {
    return <CircularProgress />;
  }

  return (
    <TableContainer component={Paper}>
      <Table aria-label="products table">
        <StyledTableHead>
          <TableRow>
            {properties.map((property) => (
              <TableCell key={property.id}>{property.name}</TableCell>
            ))}
            <TableCell>Actions</TableCell>
          </TableRow>
        </StyledTableHead>
        <TableBody>
          {products.map((product) => (
            <TableRow key={product.id} sx={{ backgroundColor: '#f3f3f3' }}>
              {properties.map((property) => (
                <TableCell key={`${product.id}-${property.id}`}>
                  { product.property_values.find((p) => p.property_id === property.id)?.value || 'N/A' }
                </TableCell>
              ))}
                <TableCell>
                  {/* <IconButton onClick={() => handleEdit(product)} color="primary">
                    <EditIcon />
                  </IconButton> */}
                  <IconButton onClick={() => handleDelete(product.id)} color="error">
                    <DeleteIcon />
                  </IconButton>
                </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default ProductList;
