import { useState, useEffect, useCallback } from 'react';
import { useSelector } from 'react-redux';
import { RootState, useAppDispatch } from 'store/store';
import { MenuItem, TextField, Select, InputLabel, FormControl, Box } from '@mui/material';
import { Operator } from 'models/property.models';
import Button from 'components/Button';
import { setFilter } from 'store/slices/filterSlice';


const formControlStyles = { minWidth: '250px', mr: 2, mb: 2 };

const PropertyFilter = () => {
  const properties = useSelector((state: RootState) => state.propertiesState.properties);
  const dispatch = useAppDispatch();
  
  const [selectedProperty, setSelectedProperty] = useState<string>('');
  const [selectedOperator, setSelectedOperator] = useState<string>('');
  const [valueInput, setValueInput] = useState<string>('');
  const [availableOperators, setAvailableOperators] = useState<Operator[]>([]);
  const [availableValuesForEnum, setAvailableValuesForEnum] = useState<string[]>([]);
  const [operatorNeedsValue, setOperatorNeedsValue] = useState<boolean>(true);
  const [isMultiSelect, setIsMultiSelect] = useState<boolean>(true);
  const [multiSelectValues, setMultiSelectValues] = useState<string[]>([]);

  useEffect(() => {
    const currentProperty = properties.find(p => p.id === parseInt(selectedProperty));
    if (currentProperty) {
      const hasValueList = (currentProperty?.values?.length || 0 ) > 0;
      setAvailableOperators(hasValueList ? currentProperty.type.operators : currentProperty.type.operators.filter(op => !op.multiple));
      setAvailableValuesForEnum(currentProperty.values || []);
    }
  }, [selectedProperty, properties]);

  useEffect(() => {
    const operator = availableOperators.find(op => op.id === parseInt(selectedOperator));
    setOperatorNeedsValue(operator?.needs_value !== false);
    setIsMultiSelect(!!operator?.multiple)
  }, [selectedOperator, availableOperators]);

  const applyFilter = useCallback(() => {
    dispatch(setFilter({ selectedProperty, selectedOperator, valueInput, multiSelectValues }));
  }, [dispatch, selectedProperty, selectedOperator, valueInput, multiSelectValues]);

  useEffect(() => {
    applyFilter();
  }, [applyFilter]);

  const handlePropertyChange = useCallback((event: any) => {
    setSelectedProperty(event.target.value);
    setSelectedOperator('');
    setValueInput('');
  }, []);

  const handleOperatorChange = useCallback((event: any) => {
    setSelectedOperator(event.target.value);
    setMultiSelectValues([]);
    setValueInput('');
  }, []);

  const handleValueChange = useCallback((event: any) => {
    setValueInput(event.target.value);
  }, []);

  const handleMultipleValuesChange = useCallback((event: any) => {
    setMultiSelectValues(event.target.value);
    setValueInput(event.target.value.join(', '));
  }, []);

  const clearFilters = useCallback(() => {
    setSelectedProperty('');
    setSelectedOperator('');
    setValueInput('');
    setIsMultiSelect(false);
    setMultiSelectValues([]);
  }, []);

  return (
    <Box sx={{ display: 'flex', flexWrap: 'wrap' }}>
      <FormControl sx={{ ...formControlStyles }}>
        <InputLabel>Property</InputLabel>
        <Select sx={{textTransform: 'capitalize'}} value={selectedProperty} label="Property" onChange={handlePropertyChange}>
          {properties.map((property) => (
            <MenuItem sx={{textTransform: 'capitalize'}} key={property.id} value={String(property.id)}>{property.name}</MenuItem>
          ))}
        </Select>
      </FormControl>
      

      <FormControl sx={{ ...formControlStyles }}>
        <InputLabel>Operator</InputLabel>
        <Select sx={{textTransform: 'capitalize'}} value={selectedOperator} label="Operator" onChange={handleOperatorChange} disabled={selectedProperty === null || selectedProperty === undefined}>
          {availableOperators?.length && availableOperators.map((operator, index) => (
            <MenuItem sx={{textTransform: 'capitalize'}} key={index} value={operator.id}>{operator.text}</MenuItem>
          ))}
        </Select>
      </FormControl>

      
      { selectedOperator !== '' && operatorNeedsValue &&
        ( availableValuesForEnum.length > 0 ? 
          (
            isMultiSelect ? (
            <FormControl sx={{ ...formControlStyles }}>
              <InputLabel>Values</InputLabel>
              <Select multiple value={multiSelectValues} label="Values" onChange={handleMultipleValuesChange} disabled={!operatorNeedsValue}>
                {availableValuesForEnum.map((value, index) => (
                  <MenuItem key={index} value={value}>{value}</MenuItem>
                ))}
              </Select>
            </FormControl>
              ) : (
                <FormControl sx={{ ...formControlStyles }}>
                  <InputLabel>Value</InputLabel>
                  <Select value={valueInput} label="Values" onChange={handleValueChange} disabled={!operatorNeedsValue}>
                    {availableValuesForEnum.map((value, index) => (
                      <MenuItem key={index} value={value}>{value}</MenuItem>
                    ))}
                  </Select>
                </FormControl>
                )
          ) : (
            <TextField 
              label="Value" 
              sx={{ ...formControlStyles }}
              value={valueInput} 
              onChange={handleValueChange} 
              disabled={!operatorNeedsValue}
            />
          )
        )
      }

      <Button onClick={clearFilters} sx={{ mb: 2, mx: 0, textTransform: 'capitalize', backgroundColor: 'rgb(0, 196, 255)'}}>
        Clear
      </Button>

    </Box>
  );
};

export default PropertyFilter;
