import { FilterState } from "store/slices/filterSlice";
import { Product } from "../models/product.models";
import { datasource } from "./datasource";
import { applyOperation, fetchOperators } from "./operatorService";

export const fetchProducts = async (filters: FilterState): Promise<Product[]> => {
  const products: Product[] = await getFilteredProducts(filters);
  return Promise.resolve(products);
};

const getFilteredProducts = async (filter: FilterState) => {
  const products: Product[] = datasource.getProducts();
  if(filter.selectedProperty === '' || filter.selectedOperator === '')  
    return products;

  const operators = await fetchOperators();
  const operator = operators.find(op => op.id === Number(filter.selectedOperator));
 
  const filtered = (operator?.needs_value && !filter.valueInput) ? products : products.filter(product => isFiltered(product, filter));

  return filtered;
}

const isFiltered = (product: Product, filter: FilterState) => {
  const property = product.property_values.find(prop => prop.property_id === Number(filter.selectedProperty));
  return applyOperation(property, filter);
}

export const deleteProductById = async (id: number) => {
  datasource.removeProduct(id);
}