import { createSlice, PayloadAction, createAsyncThunk } from '@reduxjs/toolkit';
import { Property } from 'models/property.models';
import { fetchProperties } from 'api/propertyService';
import { RootState } from 'store/store';


interface PropertyState {
  properties: Property[];
  status: 'idle' | 'loading' | 'succeeded' | 'error';
  error: string | null;
  selectedPropertyId: number | null;
}

const initialState: PropertyState = {
  properties: [],
  status: 'idle',
  error: null,
  selectedPropertyId: null
};

export const fetchPropertiesAsync = createAsyncThunk(
  'properties/fetchProperties',
  async () => {
    const response = await fetchProperties();
    return response;
  }
);

const propertySlice = createSlice({
  name: 'properties',
  initialState,
  reducers: {
    selectProperty(state, action: PayloadAction<number>) {
      state.selectedPropertyId = action.payload;
    },
    clearSelectedProperty(state) {
      state.selectedPropertyId = null;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchPropertiesAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchPropertiesAsync.fulfilled, (state, action: PayloadAction<Property[]>) => {
        state.status = 'succeeded';
        state.properties = action.payload;
      })
      .addCase(fetchPropertiesAsync.rejected, (state, action) => {
        state.status = 'error';
        state.error = action.error.message || null;
      });
  },
});

export const { selectProperty, clearSelectedProperty } = propertySlice.actions;

export default propertySlice.reducer;


// Selectors

// Selector to get all properties
export const selectAllProperties = (state: RootState) => state.propertiesState.properties;

// Selector to find a property by ID
export const selectPropertyById = (state: RootState, propertyId: number | undefined) =>
propertyId ? selectAllProperties(state).find(property => property.id === propertyId) : undefined;