import { createSlice } from '@reduxjs/toolkit';
import { RootState } from 'store/store';

export interface FilterState {
  selectedProperty: string,
  selectedOperator: string,
  valueInput: string,
  multiSelectValues: string[]
}

const initialState: FilterState = {
  selectedProperty: '',
  selectedOperator: '',
  valueInput: '',
  multiSelectValues: []
}

export const filterSlice = createSlice({
  name: 'filter',
  initialState,
  reducers: {
    setFilter: (state, action) => {
      state.selectedProperty = action.payload.selectedProperty;
      state.selectedOperator = action.payload.selectedOperator;
      state.valueInput = action.payload.valueInput;
      state.multiSelectValues = action.payload.multiSelectValues || [];
    },
    clearFilter: (state) => {
      state.selectedProperty = '';
      state.selectedOperator = '';
      state.valueInput = '';
      state.multiSelectValues = [];
    },
  },
});

export const { setFilter, clearFilter } = filterSlice.actions;

export default filterSlice.reducer;

// Selectors

export const selectFilters = (state: RootState) => state.filterState;