import React from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import styled from '@emotion/styled';

interface HeaderProps {
  title: string;
  logoSrc: string; 
}

const LogoBoxContainer = styled(Box)`
  display: flex;
  align-items: center;
`;

const LogoImage = styled('img')`
  height: 50px;
`;

const ToolbarStyled = styled(Toolbar)`
  :before {
    background-image: linear-gradient(81deg,#8a25f9,#c11c86);
    -webkit-box-shadow: 8px 13px 17px 0 rgba(0,0,0,.15);
    box-shadow: 8px 13px 17px 0 rgba(0,0,0,.15);
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -1;
`;


const Header: React.FC<HeaderProps> = ({ title, logoSrc }) => {
  return (
    <AppBar position="static" sx={{ backgroundColor: 'transparent'}}>
      <ToolbarStyled>
        
        <LogoBoxContainer>
          <LogoImage src={logoSrc} alt="Logo" />
        </LogoBoxContainer>

        <Typography variant="h6" component="div" sx={{ marginLeft: 'auto' }}>
          {title}
        </Typography>

      </ToolbarStyled>
    </AppBar>
  );
};

export default Header;
