import { OperatorEnum } from "models/operation.enum";
import { Operator } from "../models/property.models";
import { datasource } from "./datasource";
import { PropertyValue } from "models/product.models";
import { FilterState } from "store/slices/filterSlice";

export const fetchOperators = async (): Promise<Operator[]> => {
  const operators: Operator[] = datasource.getOperators() as Operator[];
  return Promise.resolve(operators);
};

export const fetchOperatorsByIds = (ids: number[]): Operator[] => {
  return datasource.operators.filter(operator => ids.includes(operator.id));
};

export const applyOperation = (property: PropertyValue | undefined, filter: FilterState) => {
  switch(Number(filter.selectedOperator) as OperatorEnum) {
    case OperatorEnum.equals: 
      return property && property.value.toLocaleString().toLocaleLowerCase() === filter.valueInput.toLocaleLowerCase();
    case OperatorEnum.greater_than:
      return property && Number(property.value) > Number(filter.valueInput);
    case OperatorEnum.less_than:
      return property && Number(property.value) < Number(filter.valueInput);
    case OperatorEnum.any:
      return property && property.value !== undefined && property.value !== null;
    case OperatorEnum.none:
      return !property || property.value === undefined || property.value === null;
    case OperatorEnum.contains:
      return property && property.value.toLocaleString().toLocaleLowerCase().includes(filter.valueInput?.toLocaleLowerCase());
    case OperatorEnum.in:
      return property && property.value?.toLocaleString() && filter.multiSelectValues.find(val => val.toLocaleLowerCase() === property.value.toLocaleString().toLocaleLowerCase());
  }
}