import { createSlice, PayloadAction, createAsyncThunk } from '@reduxjs/toolkit';
import { Product } from 'models/product.models'; 
import { fetchProducts, deleteProductById } from 'api/productService';
import { RootState } from '../store';
import { FilterState } from './filterSlice';

interface ProductState {
  products: Product[];
  status: 'idle' | 'loading' | 'succeeded' | 'error';
  error: string | null;
}

const initialState: ProductState = {
  products: [],
  status: 'idle',
  error: null,
};

export const fetchProductsAsync = createAsyncThunk<Product[], FilterState>(
  'products/fetchProducts',
  async (filters, { rejectWithValue }) => {
    try {
      const response = await fetchProducts(filters);
      return response;
    } catch (err) {
      return rejectWithValue('Failed to fetch products');
    }
  }
);

export const deleteProductAsync = createAsyncThunk<Product[], number>(
  'products/deleteProduct',
  async (id,  { rejectWithValue, getState }) => {
    try {
      await deleteProductById(id);
      const { filterState } = getState() as RootState;

      const updatedProducts = await fetchProducts(filterState);
      return updatedProducts;
    } catch (err) {
      return rejectWithValue('Failed to fetch products');
    }
  }
);

const productSlice = createSlice({
  name: 'products',
  initialState,
  reducers: {
    addProduct: (state, action: PayloadAction<Product>) => {
      state.products.push(action.payload);
    },
    removeProductById: (state, action: PayloadAction<number>) => {
      state.products = state.products.filter(product => product.id !== action.payload);
    },
    editProduct: (state, action: PayloadAction<Product>) => {
      const index = state.products.findIndex(product => product.id === action.payload.id);
      if (index !== -1) {
        state.products[index] = action.payload;
      }
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchProductsAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchProductsAsync.fulfilled, (state, action: PayloadAction<Product[]>) => {
        state.status = 'succeeded';
        state.products = action.payload;
      })
      .addCase(fetchProductsAsync.rejected, (state, action) => {
        state.status = 'error';
        state.error = action.payload as string;
      })
      .addCase(deleteProductAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(deleteProductAsync.fulfilled, (state, action: PayloadAction<Product[]>) => {
        state.status = 'succeeded';
        state.products = action.payload;
      })
      .addCase(deleteProductAsync.rejected, (state, action) => {
        state.status = 'error';
        state.error = action.payload as string;
      });
  },
});

export const { addProduct, removeProductById } = productSlice.actions;

export default productSlice.reducer;


// Selectors

// Selector to get the list of products
export const selectAllProducts = (state: RootState) => state.productsState.products;

// Selector to find a product by ID
export const selectProductById = (state: RootState, productId: number) =>
  selectAllProducts(state).find(product => product.id === productId);